*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 10px;
  font-family: "Inter", sans-serif;
}

#root {
  position: relative;
  height: auto;
  background-image: linear-gradient(
      to bottom,
      rgba(132, 45, 190, 0.52),
      rgba(68, 11, 54, 0.73)
    ),
    url("./resources/healthy1.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
