.homeFirstSection {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 80rem;
  width: 100%;
  border-bottom: 1px solid whitesmoke;
  color: #4d4a4a;
  background-image: linear-gradient(
      to bottom,
      rgba(132, 45, 190, 0.52),
      rgba(68, 11, 54, 0.73)
    ),
    url("../../../resources/healthy1.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.textContainer--homeFirstSection {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3rem 2rem;
  flex-grow: 1;
  margin: 0 1rem;
  height: 250px;
  border-radius: 0.5rem;
}

.homeFirstSection h1 {
  font-size: 3rem;
  color: whitesmoke;
}

.homeFirstSection p {
  font-size: 1.5rem;
  color: whitesmoke;
}

@media only screen and (max-width: 480px) {
  .homeFirstSection {
    flex-wrap: wrap;
    align-content: center;
  }
  .textContainer--homeFirstSection {
    width: 100%;
    margin: 0;
    padding: 1rem;
    text-align: center;
  }
}
