.aboutFirstSection {
  height: 75rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  align-content: center;
  color: whitesmoke;
  gap: 2rem;
  background-image: linear-gradient(
      to bottom,
      rgba(132, 45, 190, 0.52),
      rgba(68, 11, 54, 0.73)
    ),
    url("../../../resources/healthy2.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.textContainer {
  text-align: left;
  height: auto;
  width: 70%;
  margin: 0 1rem;
}

.aboutFirstSection h1 {
  font-size: 3rem;
}

.aboutFirstSection p {
  margin-top: 0.5rem;
  font-size: 1.7rem;
}

.aboutImg {
  width: auto;
  border-radius: 2rem;
  max-width: 100%;
  height: 250px;
}

.aboutSecondSection {
  display: flex;
  align-items: center;
  height: 85rem;
  width: 100%;
  background: #f4f5f3;
}

.aboutSecondSection--text {
  color: #4d4a4a;
  margin-left: 5rem;
  height: 77%;
  width: 100%;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-shadow: 1px 1px 5px 1px rgba(0, 0, 0, 0.2);
  border-radius: 1rem;
  transition: 1s;
}
.aboutSecondSection--text:hover {
  transform: translateY(-1rem);
  background-color: #7d43e9;
  color: whitesmoke;
}

.aboutSecondSection--text h1 {
  font-size: 4rem;
}

.aboutSecondSection--text p {
  font-size: 1.5rem;
}

.plans {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}

.plans-cont {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  box-shadow: -1px 0px 8px 0px rgba(0, 0, 0, 0.2);
  background: #8e0be6bb;
  padding: 1rem;
  width: 90%;
  min-height: 150px;
  border-radius: 1rem;
  color: whitesmoke;
  transition: 1s;
}

.plans-cont:hover {
  transform: translateY(-1rem);
  background: #9b72e7;
}

.plans .plans-cont h1 {
  font-size: 1.3rem;
}

.plans .plans-cont p {
  margin-top: 0.9rem;
  font-size: 1.1rem;
}

@media only screen and (max-width: 768px) {
}

@media only screen and (max-width: 760px) {
  .aboutFirstSection {
    flex-wrap: wrap;
  }
  .textContainer {
    width: 90%;
    height: auto;
    margin: 0;
  }
  .textContainer h1 {
    font-size: 2rem;
  }
  .textContainer p {
    font-size: 1.2rem;
  }
  .aboutImg {
    height: auto;
    max-width: 90%;
  }

  .aboutSecondSection {
    height: 110rem;
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 12rem;
  }
  .aboutSecondSection--text {
    height: 25rem;
    margin-left: 0rem;
    margin: 2rem;
  }
  .plans {
    height: 50%;
  }
}
