.footer {
  position: absolute;
  bottom: 0;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-around;
  height: 50px;
  background-color: rgba(157, 62, 180, 0.582);
}

.footer p {
  font-size: 1.5rem;
  color: whitesmoke;
  width: 100%;
  margin: 0 0 0 1rem;
}

.footer--list {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-basis: 300px;
  font-size: 2rem;
  list-style: none;
}

.footer--list li a:link {
  color: black;
}

.footer--list li a:visited {
  color: gray;
}

.footer--list li a i:hover {
  color: #15bd39;
  transition: 1s;
  border-radius: 2rem;
}

.footer--list li a i:active {
  color: greenyellow;
  transition: 0.2s;
}

.footer--list li a i {
  font-size: 1.5rem;
  color: whitesmoke;
}
