.header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;
  background-color: rgba(157, 62, 180, 0.582);
}

.header .logo {
  font-size: 3rem;
  flex-basis: 20%;
  color: whitesmoke;
}

.header .navBar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-basis: 40rem;
  list-style: none;
}

.header .navBar li a:link {
  font-size: 1.5rem;
  padding: 0.5rem;
  text-decoration: none;
  color: whitesmoke;
  border-radius: 0.5rem;
  transition: 1s;
}

.header .navBar li a:visited {
  color: whitesmoke;
}

.header .navBar li a:hover {
  background-color: whitesmoke;
  color: rgba(30, 39, 30, 0.829);
}

.header .navBar li:active {
  color: #4aa884;
  background-color: rgba(163, 143, 143, 0.301);
}

.navBarMobile {
  border: none;
  display: none;
  padding: 0 0.3rem;
  margin: 0.5rem 1rem;
  transition: 1s;
  background: none;
  cursor: pointer;
}

.rotate {
  transform: rotate(-50deg);
}

.navBarMobileHide {
  display: none;
  transform: translateX(-50px);
  transition: 1s;
  opacity: 0;
}

.line {
  background: whitesmoke;
  margin: 0.4rem 0;
  height: 4px;
  width: 24px;
  list-style: none;
  transition: 1s;
}

#line-1-rotate {
  transform: rotate(95deg) translate(0.9rem, 0.1rem);
}

#line-2-rotate {
  opacity: 0;
}

#line-3-rotate {
  transform: rotate(182deg) translate(0.24rem, 0.7rem);
}

.mobileMenu {
  width: 100%;
  position: absolute;
  top: 4.6rem;
  transition: 1s;
  overflow-y: hidden;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  overflow-y: hidden;
  opacity: 0;
  height: 3rem;
  background-color: rgba(157, 62, 180, 0.582);
  font-size: 1.3rem;
  list-style: none;
}

.link-mobile {
  text-decoration: none;
  color: whitesmoke;
}

.active-mobileMenu {
  transform: translateY(-500%);
}

.no-active-mobileMenu {
  transform: translateY(0);
  opacity: 1;
}
.mobileMenu li {
  color: whitesmoke;
}

.mobileMenuDisplay li a:link {
  font-size: 1.5rem;
  padding: 0.5rem;
  text-decoration: none;
  color: whitesmoke;
  border-radius: 0.5rem;
  transition: 1s;
}

.mobileMenuDisplay a:visited {
  color: whitesmoke;
}

.mobileMenuDisplay a:hover {
  background-color: whitesmoke;
  color: rgba(30, 39, 30, 0.829);
}

.mobileMenuDisplay a:active {
  color: #4aa884;
  background-color: rgba(163, 143, 143, 0.301);
}

.mobileMenuButtom {
  width: 3rem;
  font-size: 1.5rem;
  border: none;
  background-color: transparent;
  color: whitesmoke;
  border-radius: 0.5rem;
  font-size: 2rem;
}

.mobileMenuButtom:hover {
  width: 3rem;
  border: none;
  color: rgba(44, 51, 44, 0.829);
  background-color: whitesmoke;
  transition: 1.5s;
}

@media only screen and (max-width: 768px) {
  .header .navBar {
    flex-basis: 30rem;
  }
}

@media only screen and (max-width: 480px) {
  .header {
    justify-content: space-between;
  }

  .header h1 {
    margin: 0.5rem 1rem;
  }
  .header .navBar {
    display: none;
  }
  .navBarMobile {
    display: block;
  }
}
