.form--container {
  height: 85rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(44, 51, 44, 0.829);
  background-image: linear-gradient(
      to bottom,
      rgba(132, 45, 190, 0.52),
      rgba(68, 11, 54, 0.73)
    ),
    url("../../../resources/healthy3.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.form--container h1 {
  font-size: 2rem;
  margin-bottom: 3rem;
  color: whitesmoke;
}

.form--inner--container {
  height: 50%;
  width: 70%;
  padding: 10rem 5rem;
  border-radius: 2rem;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
  border: 0.1rem solid whitesmoke;
}

.input-text {
  color: whitesmoke;
}

.input-text,
input {
  font-size: 2rem;
  padding: 0.2rem;
  border-radius: 0.5rem;
  margin: 1rem 0;
  border: none;
}

.buttomSubmit {
  width: 10rem;
  border-radius: 0.5rem;
  height: 3rem;
  font-size: 2rem;
  border: none;
  transition: 1s;
  border: 1px solid whitesmoke;
}

.buttomSubmit:link {
  background-color: whitesmoke;
}

.buttomSubmit:hover {
  background-color: rgba(162, 27, 216, 0.829);
  color: whitesmoke;
}

.buttomSubmit:active {
  background-color: whitesmoke;
  color: rgba(217, 0, 255, 0.829);
  transform: translateY(5px);
}

@media only screen and (max-width: 768px) {
  .form--inner--container {
    width: 90%;
    height: 60%;
  }
  .input-text,
  input {
    font-size: 1.5rem;
    width: 100%;
  }
}
